import { useState } from "react";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import CreateButton from "../../../../Ui/Button/CreateButton";
import style from "../../StoreManager.module.css";
import StoreIssueGoods from "../../StoreIssue/StoreIssueGoods/StoreIssueGoods";
const StoreSevenRegisterCard = ({ register, flow }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      {flow === "Custom" ? (
        <div className={style.smc_card}>
          <div
            className={style.smc_card_contatiner}
            style={{
              justifyContent: "space-between",
              width: "100%",
            }}
          >
            <div className={style.smc_card_contatiner}>
              <img
                src={
                  register?.good_head_person?.staffProfilePhoto
                    ? `${imageShowUrl}/${register?.good_head_person?.staffProfilePhoto}`
                    : `${assestsFeedUrl}/feed-user-avatar.svg`
                }
                loading="lazy"
                alt="avatar"
                style={{
                  height: "2.3rem",
                  width: "2.3rem",
                  borderRadius: "50%",
                }}
              />

              <div className={style.smc_card_contatiner_inner}>
                <h6>
                  {t("good_title_person")} {": "}{" "}
                  {register?.good_title_person ?? ""}
                  {", "}
                  {t("good_head_name")} {": "} {register?.good_head_name ?? ""}
                </h6>

                <p
                  style={{
                    fontSize: "12px",
                  }}
                >
                  {t("issue_on")} {": "} {dayjs().format("DD/MM/YYYY")}
                </p>
              </div>
            </div>
            <CreateButton
              label="issue_goods"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </div>
      ) : (
        "Hi statics"
      )}
      {open && <StoreIssueGoods irid={register?._id} onClose={onToggle} />}
    </>
  );
};

export default StoreSevenRegisterCard;
